.footer {
    padding: 30px 0;
    background-color: var(--secondaryBackgroundColor);
}

.footer-thumb {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.copyright {
    font-size: 14px;
    color: var(--accentColor);

    @media screen and (min-width: 1200px) {
        font-size: 24px;
    }
}